.sb-container {
  font-style: italic;
  max-width: 1200px;
  width: 95%;
  margin: 0 auto;
  margin-top: 10px;
}

.sb-form {
  background-color: rgb(255, 255, 255);
  width: 60%;
  height: 50px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.sb-form input {
  background-color: black;
  cursor: text;
  width: 100%;
  height: 100%;
  color: rgb(255, 255, 255);
  padding-top: 20px;
  border: none;
  outline: none;
}
.sb-form label {
  background-color: rgb(252, 252, 252);
  background: transparent;
  pointer-events: none;
  position: absolute;
  bottom: 0px;
  left: 0%;
  width: 100%;
  height: 100%;
  color: white;
  border-bottom: 1px solid rgb(255, 255, 255);
  transition: border 0.3s ease;
}
@media screen and (max-width: 353px) {
  .sb-form label {
    font-size: 15px;
  }
}
@media screen and (max-width: 312px) {
  .sb-form label {
    font-size: 14px;
  }

  .sb-form input:focus + .sb-label-name .sb-content-name {
    font-size: 13px;
  }
}
.sb-form label::after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 1.5px solid rgba(255, 0, 0, 0.6);
  transform: translateX(-100%);
}
.sb-content-name {
  background-color: rgb(0, 0, 0);
  color: rgba(255, 255, 255, 0.281);
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all 0.3s ease;
}
.sb-form input:focus + .sb-label-name .sb-content-name {
  transform: translateY(-150%);
  font-style: normal;
  font-size: 14px;
  color: rgb(255, 255, 255);
}
.sb-form input:focus + .sb-label-name::after {
  transform: translateX(0%);
}
